import { decideFormat } from './articleFormat';
import { getDataLinkNameCard } from './getDataLinkName';
export const decideTrail = (trail, index = 0) => {
    const format = decideFormat(trail.format);
    const image = trail.image
        ? {
            src: trail.image,
            altText: '', // TODO: Do we get this from frontend?
        }
        : undefined;
    return {
        ...trail,
        image,
        format,
        dataLinkName: getDataLinkNameCard(format, '0', index),
    };
};
